
import { IonPage, IonLoading, modalController } from "@ionic/vue"
import { defineComponent, onMounted, ref } from 'vue'
import { v2ColumnInterface, v2TableBadge } from '@/components/DataViews/tables/v2PocDatatable/types';
import { alertConfirmation, toastDanger, toastWarning } from '@/utils/Alerts';
import { DisaggregatedReportService } from "@/apps/ART/services/reports/disaggregated_service"
import v2Datatable from "@/components/DataViews/tables/v2PocDatatable/TableView.vue"
import ArtDrilldown from "@/apps/ART/Components/ArtDrilldown.vue";
import { REGIMENS } from "@/apps/ART/services/reports/regimen_report_service";
import { AGE_GROUPS } from "@/apps/ART/services/reports/patient_report_service"
import { barChart, thumbsUp, thumbsDown, bug, informationCircle } from "ionicons/icons"
import Img from "@/utils/Img";
import { uniq } from "lodash";
import { useRoute } from "vue-router";
import { toDate } from "@/utils/Strs";
import { MohCohortReportService } from "@/apps/ART/services/reports/moh_cohort_service"
import ReportErrors from "@/apps/ART/Components/ReportErrors.vue"

export default defineComponent({
    components: { 
        IonPage,
        IonLoading,
        v2Datatable
    },
    setup() {
        const configOk = ref(false)
        const reportData = ref<any>([])
        const period = ref('')
        const loadingMessage = ref('')
        const isLoading = ref(false)
        const report = new DisaggregatedReportService()
        const mohService = new MohCohortReportService()
        const headerBadge = ref<v2TableBadge[]>([])
        const route = useRoute()

        const drill = async (props: any) => {
            (await modalController.create({
                component: ArtDrilldown,
                backdropDismiss: false,
                cssClass: 'large-modal',
                componentProps: {
                    ...props,
                    subtitle: period.value,
                    onFinish: () => modalController.getTop().then(v => v && modalController.dismiss())
                }
            })).present();
        }
    
        const toDrillColumn = (label: string, ref: string) => {
            return {
                ref, label,
                toValue: (data: any) => data.length,
                tdClick: async (row: any) => {
                    if(row.refData.length) drill({
                        patientIdentifiers: row.refData,
                        title: `${row.data.age_group} ${row.data.gender} ${row.column.label}`,
                    })
                }
            }
        }

        const columns: Array<v2ColumnInterface[]> = [
            [
                { label: "#", ref: "index" },
                { label: "Age group", ref: 'age_group' },
                { label: "Gender", ref: 'gender' },
                toDrillColumn("TX curr (receiving ART)", "tx_curr"),
                ...REGIMENS.reduce((a: any, regimen: string) => {
                    return [...a, toDrillColumn(regimen, regimen)]
                }, []),
                toDrillColumn("Unknown", "N/A"),
                toDrillColumn("Total (regimen)", "regimen_total")
            ]
        ]

        const DEFAULT_REGIMENS = [...REGIMENS, 'N/A'].reduce((a: any, c: any) => ({...a, [c]: []}), {})

        const onRegenerate = async () => generate((await alertConfirmation("Do you want to rebuild report?")))

        const updateHeaderBadge = function (badge: v2TableBadge) {
            const index =  headerBadge.value.findIndex(obj => obj.id === badge.id);
            if (index !== -1) {
                headerBadge.value[index] = badge
            } else {
                headerBadge.value.push(badge)
            }
        }

        const showErrors = async function(errors: string[]) {
            const id = 'validationErrors';
            (await modalController.create({
                id,
                component: ReportErrors,
                backdropDismiss: false,
                cssClass: "large-modal",
                componentProps: {
                    errors
                }
            })).present();
            updateHeaderBadge({
                id,
                text: `<b>${errors.length}</b> validation errors detected`,
                icon: thumbsDown,
                color: "danger",
                action: () => showErrors(errors)
            })
        }

        const onValidate = async (totalAlive: number[]) => {
            const validations: any = {
                'total_alive_and_on_art' : {
                    param: totalAlive.length,
                    check: (i: number, p: number) => i != p,
                    error: (i: number, p: number) => `
                        Total alive of <b>${p}</b>
                        Does not match total alive of <b>${i}</b> on MOH report
                    `
                }
            }
            const s = mohService.validateIndicators(validations, (errors: string[]) => {
                if (errors.length) { 
                    showErrors(errors) 
                } else {
                    updateHeaderBadge({
                        id: "reportOk",
                        text: "<b>Report is consistent</b>",
                        icon: thumbsUp,
                        color: "success"
                    })
                }
            })
            if (s === -1) showErrors(['Report not validated. Run the MoH cohort report for similar reporting period and then run this report'])
        }

        const generate = async (rebuild=false) => {
            if (!configOk.value) {
                return toastDanger("Unable to generate report, return to cohort report")
            }
            isLoading.value = true
            const PREGNANCY_AGE_GROUPS = ['Pregnant', 'Breastfeeding']
            const meta: any = {
                F: { label: "Female", rows: [], aggregate: {}, excludedAgeGroups: PREGNANCY_AGE_GROUPS },
                M: { label: "Male", rows: [], aggregate: {}, excludedAgeGroups: PREGNANCY_AGE_GROUPS },
                FP: { label: "Pregnant", rows: [], aggregate: {}, gender: 'F', ageGroup: "All", 
                    genderDisplay: 'FP', excludedAgeGroups: [...AGE_GROUPS, 'Breastfeeding'] },
                FBF: { label: "Breastfeeding", rows: [], aggregate: {}, gender: 'F', 
                    ageGroup: 'All', genderDisplay: 'FBF', excludedAgeGroups: [...AGE_GROUPS, "Pregnant"] },
            }

            const buildData = (completed=false) => {
                const allFp: any = Object.keys(meta.FP.aggregate).reduce((a: any, k: any) => {
                    return { ...a, [k]: [...meta.FP.aggregate[k]||[], ...meta.FBF.aggregate[k]||[]] }
                }, {})
                const fnp = Object.keys(meta.F.aggregate).reduce((a: any, k: any) => {
                    return { ...a, [k]: a[k].filter((id: number) => Array.isArray(allFp[k])
                        ? !allFp[k].includes(id)
                        : true)
                    }
                }, meta.F.aggregate)
                reportData.value = [
                    ...meta.F.rows, ...meta.M.rows,
                    { age_group: 'All', gender: "Male", ...meta.M.aggregate },
                    ...meta.FP.rows, 
                    { age_group: 'All', gender: "FNP", ...fnp },
                    ...meta.FBF.rows
                ]
                const total = uniq([...(meta.M.aggregate.tx_curr||[]), ...(meta.F.aggregate.tx_curr||[])])
                const totalAliveBadge: v2TableBadge = {
                    text: `Total alive and on ART: <b>${total.length}</b>`, 
                    icon: barChart,
                    color: "primary",
                    action: () => drill({
                        patientIdentifiers: total,
                        title: `Total alive and on ART: ${total.length}`,
                    })
                }
                if (!completed) {
                    headerBadge.value = [ totalAliveBadge, {
                        text: loadingMessage.value,
                        icon: informationCircle,
                        color: "dark"
                    }]
                } else {
                    headerBadge.value = [ totalAliveBadge ]
                    onValidate(total)
                }
            }
            try {
                report.setRebuildOutcome(rebuild)
                if (!(await report.init())) return toastWarning("Unable to initialise report")
                for(const age of [...AGE_GROUPS, ...PREGNANCY_AGE_GROUPS]) {
                    loadingMessage.value = `Processing <b>${age}</b>`
                    report.setAgeGroup(age)
                    const res = await report.getCohort()
                    report.setRebuildOutcome(false)
                    for(const gender of Object.keys(meta)) {
                        if ((meta[gender].excludedAgeGroups||[]).includes(age)) continue
                        report.setGender(gender)
                        if (meta[gender].ageGroup) report.setAgeGroup(meta[gender].ageGroup)
                        const regimens = (await report.getRegimenDistribution())
                        const data = {
                            age_group: meta[gender]?.ageGroup || age,
                            gender: meta[gender]?.genderDisplay || meta[gender].label,
                            ...(res[age] ? res[age][meta[gender]?.gender || gender]: { tx_curr: []}),
                            ...DEFAULT_REGIMENS, ...regimens,
                            regimen_total: Object.values(regimens).flat(1)
                        }
                        meta[gender].rows.push(data)
                        meta[gender].aggregate = Object.keys(data).reduce((a: any, k: any) => {
                            return Array.isArray(data[k]) ? { ...a, [k] : [...a[k]||[], ...data[k]]} : a
                        }, meta[gender].aggregate)
                        buildData(false)
                    }
                }
                buildData(true)
                reportData.value = reportData.value.map((d:any, i: number) => ({index: i+1, ...d}))
            } catch (e) {
                const msg = "Unable to generate report!"
                toastDanger(msg)
                headerBadge.value = [{
                    text: msg,
                    color: "danger",
                    icon: bug,
                    action: function() {
                        headerBadge.value = [{
                            text: `Exception occured: <b>${e}</b>`,
                            color: "danger",
                            icon: bug
                        }]
                    }
                }]
            }
            isLoading.value = false
            loadingMessage.value = ''
        }

        onMounted(() => {
            configOk.value = (route.query.start_date && route.query.end_date && route.query.quarter) ? true : false
            if (configOk.value) {
                report.setStartDate(`${route.query.start_date}`)
                report.setEndDate(`${route.query.end_date}`)
                report.setOccupation(`${route.query.occupation}`)
                mohService.setOccupation(report.occupation)
                mohService.setStartDate(report.startDate)
                mohService.setEndDate(report.endDate)
                period.value = `${toDate(report.startDate)} to ${toDate(report.endDate)}`
                if (route.query.quarter) {
                    report.setQuarter(`${route.query.quarter}`)
                    mohService.setQuarter(report.quarter)
                    period.value = `${report.quarter === 'Custom' ? period.value : report.quarter }`
                }
                generate()
            }
        })

        return {
            Img,
            onRegenerate,
            headerBadge,
            reportData,
            isLoading,
            generate,
            columns,
            period
        }
    }
})
